import request from "./request";
//获取商品列表
export const sendListData = () => {
  return request({
    url: "/api/goods/list",
    method: "get",
  });
};
//查询商品id的数据
export const getGoodslistId = (id) => {
  return request({
    url: "/api/goods/id",
    method: "get",
    params: {
      id,
    },
  });
};
