<template>
  <div class="list">
    <header>
      <div class="returns" @click="goBanckHome">
        <i class="iconfont icon-fanhui"></i>
      </div>
      <div class="search">
        <i class="iconfont icon-fangdajing"></i>
        <input type="text" placeholder="输入框" />
      </div>
      <div class="home"><i class="iconfont icon-zhuye"></i></div>
    </header>
    <section>
      <div class="list-l">
        <ul class="list-item">
          <li
            v-for="(item, index) in listData"
            :key="item.id"
            :class="lListindex == index ? 'active' : ''"
            @click="leftLiClick(index)"
          >
            {{ item.name }}
          </li>
        </ul>
      </div>
      <div class="list-r" ref="wrapper">
        <div>
          <div class="list-r-h">
            <img
              src="https://img2.baidu.com/it/u=181794609,2918736279&fm=253&fmt=auto&app=138&f=JPEG?w=750&h=500"
              alt="茶叶"
            />
          </div>
          <div class="list-r-b" ref="right">
            <ul>
              <li class="shop-list" v-for="item in listData" :key="item.id">
                <h2>{{ item.name }}</h2>
                <ul class="r-content">
                  <li v-for="i in item.data" :key="i.id">
                    <img :src="i.imgUrl" /><span>{{ i.name }}</span>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <span v-show="false">{{ this.currentIndex }}</span>
    </section>
    <Tabber class="tabber"></Tabber>
  </div>
</template>

<script>
import Tabber from "../components/common/Tabber.vue";
import BScroll from "better-scroll"; //页面滚动插件
import { sendListData } from "@/api/list.js";
export default {
  name: "List-vue",
  components: {
    Tabber,
  },
  data() {
    return {
      listData: [], //存储后台返回的页面数据
      lListindex: 0, //存储左侧点击是的索引值,用来实现左右侧的联动
      allHeight: [240], //存储右侧各模块的高度,用来实现左右侧的联动
      rightBScroll: null,
      scrollY: 0, //存储右侧的页面滑动多少距离
    };
  },
  created() {
    this.getDataList();
  },
  computed: {
    currentIndex() {
      let i = 0;
      let sy = this.scrollY;
      while (sy > 0) {
        sy = sy - this.allHeight[i];
        i++;
        if (i > 9) {
          i = 9;
        }
      }
      this.leftLiClick(i - 11);
      return i;
    },
  },
  methods: {
    async getDataList() {
      let res = await sendListData();
      if (res.data.code == 200) {
        this.listData = res.data.data;
      }

      this.$nextTick(() => {
        this.rightBScroll = new BScroll(this.$refs.wrapper, {
          movable: true,
          zoom: true,
          click: true,
          probeType: 3,
        });
      });

      //右侧每一块元素的class为shop-list，获取到所有的元素，并获取其高度
      this.$nextTick(() => {
        let rlh = this.$refs.right.getElementsByClassName("shop-list");
        Array.from(rlh).forEach((v) => {
          this.allHeight.push(v.clientHeight);
        });
        this.rightBScroll.on("scroll", (pos) => {
          this.scrollY = Math.abs(pos.y);
        });
      });
    },

    leftLiClick(i) {
      if (i >= 0) {
        this.lListindex = i;
        //用来实现左右侧的联动
        let sumAllHeight = 0; //来计算一共要走的高度；
        //用来判断当前处于那个模块
        for (let j = 0; j <= i; j++) {
          sumAllHeight = sumAllHeight + this.allHeight[j];
        }
        this.rightBScroll.scrollTo(0, -sumAllHeight, 300);
        sumAllHeight = 0;
      } else {
        i = i + 10;
        if (i <= 0) {
          i = 0;
        }
        this.lListindex = i;
      }
    },
    //返回的箭头回调函数
    goBanckHome() {
      this.$router.push({
        path: "/home",
      });
    },
  },
};
</script>

<style scoped lang="less">
.list {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 1.174rem;
    background-color: #b0352f;
    .returns,
    .home {
      height: 100%;
      line-height: 1.174rem;
      padding: 0 0.54rem;
      i {
        color: #fff;
        font-size: 0.7rem;
      }
    }
    .search {
      flex: 1;
      padding: 0 0.26rem;
      background-color: #fff;
      border-radius: 23px;
      i {
        font-size: 0.6rem;
        color: #666;
      }
      input {
        display: inline-block;
        width: 80%;
        height: 0.6rem;
        margin-left: 0.2rem;
        vertical-align: middle;
      }
    }
  }
  section {
    flex: 1;
    overflow: hidden;
    display: flex;
    .list-l {
      flex: 2;
      overflow: hidden;
      border-right: 1px solid #ccc;
      ul {
        li {
          margin: 0.5rem 0;
          width: 100%;
          height: 1rem;
          line-height: 1rem;
          text-align: center;
          font-size: 0.5rem;
          align-items: center;
        }
      }
    }
    .list-r {
      flex: 8;
      .list-r-h {
        width: 90%;
        height: 20%;
        margin: 0.3rem auto;
        border-radius: 15px;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .list-r-b {
        width: 100%;
        height: 100%;
        overflow: hidden;

        .shop-list {
          text-align: center;
          margin-bottom: 1.3rem;
          h2 {
            font-size: 0.64rem;
            font-weight: 400;
          }
          .r-content {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            li {
              display: flex;
              width: 26%;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              margin: 0.3rem 0.2rem;
              img {
                width: 1.7rem;
                height: 1.7rem;
              }
              span {
                font-size: 0.42rem;
              }
            }
          }
        }
      }
    }
  }
  .active {
    border-left: 2px solid #b0352f;
    color: #b0352f;
  }
}
::v-deep .tabber {
  border-top: 1px solid #ccc;
}
</style>
